/* About.scss */

// Variables
$primary-color: #5318eb;
$secondary-color: #fff;
$background-dark: #3a3a3a;
$text-color-dark: #fff;
$text-color-light: #333;

// Mixins
@mixin dark-mode {
  background-color: $background-dark;
  color: $text-color-dark;
}

.about-section {
  padding: 50px 0;

  &.dark-mode {
    @include dark-mode;

    .about-circle-img img {
      border-color: $secondary-color;
    }

    .about-line-1 {
      color: $secondary-color;
    }

    .about-line-4 {
      color: $secondary-color !important;
    }
  }

  .about-circle-img {
    img {
      border-radius: 50%;
      border: 5px solid transparent;
    }
  }

  .about-lines-wrapper {
    .about-line-1 {
      color: $primary-color;
    }

    .about-line-2 {
      text-transform: uppercase;
      font-weight: 700;
      font-size: 2rem; // Default font size
    }

    .about-line-3 {
      font-weight: 600;
      font-size: 1.5rem; // Default font size
    }

    .about-line-4 {
      color: $text-color-light;
    }
  }

  @media (min-width: 768px) {
    .about-circle-img img {
      height: 400px;
      width: 400px;
    }

    .about-lines-wrapper {
      .about-line-2 {
        font-size: 50px;
      }

      .about-line-3 {
        font-size: 40px;
      }
    }
  }

  @media (max-width: 768px) {
    .about-circle-img {
      text-align: center;
    }
  }
}
