.scroll-up-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #5318eb;
  color: #fff;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s ease, transform 0.3s ease;
  z-index: 1000;

  &.visible {
    opacity: 1;
    transform: translateY(0);
  }

  &.dark-mode {
    background-color: #3a3a3a;
    color: #ffffff;
  }

  &:hover {
    background-color: darken(#5318eb, 10%);
  }
}

@media (max-width: 768px) {
  .scroll-up-button {
    width: 40px;
    height: 40px;
    font-size: 1.5rem;
    bottom: 150px !important;
  }
}
