/* HeroBanner.scss */

// Variables
$primary-color: #6b53ff;
$secondary-color: #fff;
$gradient-light: linear-gradient(90deg, #5318eb, #ab6ef9);
$gradient-dark: linear-gradient(90deg, #1f1f1f, #3a3a3a);
$font-family: "Maven Pro", sans-serif;

// Mixins
@mixin dark-mode {
  background-image: $gradient-dark;
}

.hero-banner {
  background-image: $gradient-light;
  font-family: $font-family;
  color: $secondary-color;
  text-align: center;
  padding: 100px 20px;
  position: relative;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
    background: rgba($secondary-color, 0.1);
    z-index: 1;
  }

  h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    position: relative;
    z-index: 2;
  }

  .cta-btn {
    font-size: 1rem;
    color: $primary-color;
    background-color: $secondary-color;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    position: relative;
    z-index: 2;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: $primary-color;
      color: $secondary-color;
      border: 1px solid $secondary-color;
    }
  }

  &.dark-mode {
    @include dark-mode;

    .cta-btn {
      background-color: darken($secondary-color, 10%);
      color: lighten($primary-color, 10%);

      &:hover {
        background-color: lighten($primary-color, 10%);
        color: $secondary-color;
      }
    }
  }

  @media (max-width: 768px) {
    height: 100vh;
    padding: 80px 10px;

    h1 {
      font-size: 2rem;
    }

    .cta-btn {
      font-size: 1rem;
      padding: 10px 15px;
    }
  }
}
