/* Footer.scss */

// Variables
$primary-color: #6b53ff;
$secondary-color: #fff;
$dark-background: #3a3a3a;
$text-color-light: #fff;
$text-color-dark: #333;

// Mixins
@mixin dark-mode {
  background-color: $dark-background;
  color: $text-color-light;
}

.footer {
  padding: 20px 0;
  background-color: $primary-color;
  color: $secondary-color;
  position: relative;

  &.dark-mode {
    @include dark-mode;
  }

  p {
    margin: 0;
    font-size: 1rem;
  }

  .scroll-to-top {
    background: none;
    border: none;
    color: $secondary-color;
    font-size: 1.5rem;
    cursor: pointer;
    position: absolute;
    right: 20px;
    bottom: 20px;
    transition: transform 0.3s ease;

    &:hover {
      transform: scale(1.2);
    }
  }

  .scroll-to-top i {
    display: block;
  }

  @media (max-width: 768px) {
    margin-bottom: 70px !important;
    font-size: 16px !important;

    .scroll-to-top {
      font-size: 2rem;
    }
  }
}
