/* Projects.scss */

// Variables
$primary-color: #6b53ff;
$secondary-color: #fff;
$dark-background: #3a3a3a;
$text-color-light: #fff;
$text-color-dark: #333;
$modal-background: #f4f4f4;

// Mixins
@mixin dark-mode {
  background-color: $dark-background;
  color: $text-color-light;
}

.projects-section {
  padding-bottom: 50px;

  &.dark-mode {
    @include dark-mode;
  }

  .heading {
    color: $text-color-dark;

    .dark-mode & {
      color: $text-color-light;
    }
  }

  .swiper-container {
    .swiper-slide {
      display: flex;
      justify-content: center;
    }

    .swiper-pagination {
      bottom: 10px;
      text-align: center;
      width: 100%;

      .swiper-pagination-bullet {
        background: $text-color-light;
        opacity: 0.7;

        &.swiper-pagination-bullet-active {
          background: $primary-color;
        }
      }
    }
  }

  .card {
    &.project-card {
      border-radius: 15px;
      overflow: hidden;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
      transition: transform 0.3s ease;
      background-color: $primary-color;
      color: $secondary-color;

      .card-img-container {
        overflow: hidden;
        height: 200px;
        position: relative;

        img {
          width: 100%;
          height: auto;
          transition: transform 6.5s ease;

          &:hover {
            transform: translateY(calc(200px - 100%));
            transition: transform 15s ease;
          }
        }
      }

      .card-body {
        padding: 15px;

        .card-title {
          font-size: 1.5rem;
          font-weight: bold;
        }

        .card-text {
          font-size: 1rem;
          color: hsla(0, 0%, 100%, 0.8);
        }
      }
    }
  }
}

/* Modal Styles */

.Modal {
  background: $modal-background;
  border-radius: 10px;
  padding: 2rem;
  max-width: 500px;
  margin: 2rem auto;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

  h2 {
    margin-top: 0;
  }

  p {
    margin: 1rem 0;
  }

  .btn {
    margin-right: 1rem;
  }

  &.dark-mode {
    background: $dark-background;
    color: $text-color-light;
  }
}

.Overlay {
  background: rgba(0, 0, 0, 0.75);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}
