/* Contact.scss */

// Variables
$primary-color: #6b53ff;
$dark-background: #121212;
$text-color-light: #fff;
$text-color-dark: #333;
$modal-background: #f4f4f4;

// Mixins
@mixin dark-mode {
  background-color: $dark-background;
  color: $text-color-light;
}

.contact-section {
  padding-bottom: 5rem;

  &.dark-mode {
    @include dark-mode;
  }

  .heading {
    text-align: center;
    color: $text-color-dark;

    .dark-mode & {
      color: $text-color-light;
    }
  }

  #social-media {
    text-align: center;

    a {
      padding: 20px;
      font-size: 30px;
      width: 50px;
      text-align: center;
      text-decoration: none;
      color: $primary-color;
      transition: color 0.3s ease;

      &:hover {
        color: darken($primary-color, 10%);
        text-decoration: none;
      }
    }
  }
}

/* Modal Styles */

.Modal {
  background: $modal-background;
  border-radius: 10px;
  padding: 2rem;
  max-width: 500px;
  margin: 2rem auto;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

  h2 {
    margin-top: 0;
  }

  p {
    margin: 1rem 0;
  }

  .btn {
    margin-right: 1rem;
  }

  &.dark-mode {
    background: $dark-background;
    color: $text-color-light;
  }
}

.Overlay {
  background: rgba(0, 0, 0, 0.75);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}
