/* Skills.scss */

// Variables
$primary-color: #6b53ff;
$background-light: #f8f9fa;
$background-dark: #333;
$text-color-light: #333;
$text-color-dark: #fff;

// Mixins (if needed for further styles)
@mixin dark-mode {
  background-color: $background-dark;
  color: $text-color-dark;
}

.skills-section {
  padding: 2rem 0;
  background-color: $background-light;

  &.dark-mode {
    @include dark-mode;
  }

  .heading {
    text-align: center;
    margin-bottom: 2rem;
    font-size: 2rem;
    color: $text-color-light;

    .dark-mode & {
      color: $text-color-dark;
    }
  }

  .skill-item {
    text-align: center;
    margin-bottom: 1.5rem;

    svg,
    i {
      font-size: 4rem; // Increase icon size
      color: $primary-color;
      margin-bottom: 0.5rem;
    }

    p {
      margin: 0;
      font-size: 1rem;
      color: $text-color-light;

      .dark-mode & {
        color: $text-color-dark;
      }
    }
  }
}
