.navbar.fixed-bottom .nav-link {
    text-align: center;
    color: #fff;
    font-size: 0.875rem;
}

.navbar.fixed-bottom .nav-link span {
    display: block;
    font-size: 0.75rem;
}

.dark-mode .navbar.fixed-bottom {
    background-color: #333333;
    background-image: linear-gradient(90deg, #1f1f1f, #3a3a3a);
    box-shadow: 0 2px 4px 0 rgb(0 0 0 / 50%);
}

/* Active state styles */
.nav-link {
    padding: 5px 10px;

}

.nav-link.active {
    border: 1px solid #fff;
}

/* .nav-link.active i,
  .nav-link.active span {
    color: #6b53ff;
  } */
.dark-mode .nav-link.active {
    border: 1px solid #6b53ff;
}

.dark-mode .nav-link.active i,
.dark-mode .nav-link.active span {
    color: #6b53ff;
}

.dark-mode .nav-link.active {
    color: #6b53ff;
}

.dark-mode .nav-link.active i,
.dark-mode .nav-link.active span {
    color: #6b53ff;
}